import * as vscode from 'vscode';
import { CacheManager } from './cache';
import { ConfigurationManager } from './config';
import { PythonHoverProvider } from './hoverProvider';
import { InventoryManager } from './inventory';
import { VersionDetector } from './versionDetector';
import { CodeRunner } from './codeRunner';
import { CustomDocumentationLoader } from './customDocumentation';

export function activate(context: vscode.ExtensionContext) {
    console.log('[PythonHover] 🐍 Extension activating...');

    // Initialize managers
    const configManager = new ConfigurationManager();
    const cacheManager = new CacheManager(context.globalStorageUri);
    const inventoryManager = new InventoryManager(cacheManager);
    const versionDetector = new VersionDetector(configManager);
    const codeRunner = new CodeRunner();

    // Register hover provider
    const hoverProvider = new PythonHoverProvider(
        configManager,
        inventoryManager,
        versionDetector,
        cacheManager
    );

    context.subscriptions.push(
        vscode.languages.registerHoverProvider({ language: 'python' }, hoverProvider)
    );

    // Register clear cache command
    context.subscriptions.push(
        vscode.commands.registerCommand('pythonHover.clearCache', async () => {
            try {
                const stats = await cacheManager.clear();
                await inventoryManager.invalidateCache();
                vscode.window.showInformationMessage(
                    `✅ Cache cleared! Deleted ${stats.filesDeleted} files.`
                );
            } catch (error) {
                vscode.window.showErrorMessage(`❌ Failed to clear cache: ${error}`);
                console.error('[PythonHover] Cache clear error:', error);
            }
        })
    );

    // Register command to open documentation URL (makes links clickable)
    context.subscriptions.push(
        vscode.commands.registerCommand('pythonHover.openDocs', (url: string) => {
            if (url && url.startsWith('http')) {
                vscode.env.openExternal(vscode.Uri.parse(url));
            }
        })
    );

    // Register command to copy documentation URL
    context.subscriptions.push(
        vscode.commands.registerCommand('pythonHover.copyUrl', (url: string) => {
            if (url) {
                vscode.env.clipboard.writeText(url);
                vscode.window.showInformationMessage('📋 URL copied to clipboard!');
            }
        })
    );

    // NEW: Register command to run example code
    context.subscriptions.push(
        vscode.commands.registerCommand('pythonHover.runExample', async (encodedCode: string) => {
            try {
                const code = decodeURIComponent(encodedCode);
                await codeRunner.runCode(code, false);
            } catch (error) {
                vscode.window.showErrorMessage(`❌ Failed to run code: ${error}`);
                console.error('[PythonHover] Run example error:', error);
            }
        })
    );

    // NEW: Register command to copy example code
    context.subscriptions.push(
        vscode.commands.registerCommand('pythonHover.copyExample', async (encodedCode: string) => {
            try {
                const code = decodeURIComponent(encodedCode);
                await codeRunner.copyCode(code);
            } catch (error) {
                vscode.window.showErrorMessage(`❌ Failed to copy code: ${error}`);
                console.error('[PythonHover] Copy example error:', error);
            }
        })
    );

    // NEW: Register command to insert example code
    context.subscriptions.push(
        vscode.commands.registerCommand('pythonHover.insertExample', async (encodedCode: string) => {
            try {
                const code = decodeURIComponent(encodedCode);
                await codeRunner.insertCode(code);
            } catch (error) {
                vscode.window.showErrorMessage(`❌ Failed to insert code: ${error}`);
                console.error('[PythonHover] Insert example error:', error);
            }
        })
    );

    // NEW: Register command to reload custom documentation
    context.subscriptions.push(
        vscode.commands.registerCommand('pythonHover.reloadCustomDocs', async () => {
            try {
                await hoverProvider.reloadCustomDocs();
                vscode.window.showInformationMessage('✅ Custom documentation reloaded!');
            } catch (error) {
                vscode.window.showErrorMessage(`❌ Failed to reload custom docs: ${error}`);
                console.error('[PythonHover] Reload custom docs error:', error);
            }
        })
    );

    // NEW: Register command to create sample custom docs config
    context.subscriptions.push(
        vscode.commands.registerCommand('pythonHover.createCustomDocsConfig', async () => {
            try {
                const folders = vscode.workspace.workspaceFolders;
                if (!folders || folders.length === 0) {
                    vscode.window.showErrorMessage('No workspace folder open');
                    return;
                }
                await CustomDocumentationLoader.createSampleConfigFile(folders[0]);
                vscode.window.showInformationMessage('✅ Sample configuration created!');
            } catch (error) {
                vscode.window.showErrorMessage(`❌ Failed to create config: ${error}`);
                console.error('[PythonHover] Create config error:', error);
            }
        })
    );

    // Handle configuration changes
    context.subscriptions.push(
        vscode.workspace.onDidChangeConfiguration((event: vscode.ConfigurationChangeEvent) => {
            if (event.affectsConfiguration('pythonHover')) {
                configManager.refresh();
            }
        })
    );

    // Watch for changes to custom documentation files
    const docWatcher = vscode.workspace.createFileSystemWatcher('**/{.python-hover.json,python-hover.json,.vscode/python-hover.json}');
    context.subscriptions.push(docWatcher);
    
    docWatcher.onDidChange(async () => {
        console.log('[PythonHover] Custom documentation file changed, reloading...');
        await hoverProvider.reloadCustomDocs();
    });

    docWatcher.onDidCreate(async () => {
        console.log('[PythonHover] Custom documentation file created, reloading...');
        await hoverProvider.reloadCustomDocs();
    });

    docWatcher.onDidDelete(async () => {
        console.log('[PythonHover] Custom documentation file deleted, reloading...');
        await hoverProvider.reloadCustomDocs();
    });

    // Dispose code runner on deactivation
    context.subscriptions.push(codeRunner);

    console.log('[PythonHover] ✅ Extension activated successfully');
}

export function deactivate() {
    console.log('[PythonHover] Extension deactivated');
}
